body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-equation {
    font-size: 28px;
    display: flex;
    flex-direction: row;
    align-items: center;

    white-space: nowrap;
    margin: 8px auto;

    -webkit-user-select: none;

       -moz-user-select: none;

        -ms-user-select: none;

            user-select: none;
}

.term-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 10px;
}

.equation-term {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    margin: 15px 0;
    position: relative;
}

.term-wrapper.faded {
    color: hsl(200, 10%, 70%);
}

.equation-term .arrow-container {
    position: absolute;
    height: 60px;
    margin: -30px 0;
    width: 80px;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
}

.equation-term .arrow-container.up {
    top: -10px;
}

.equation-term .arrow-container.down {
    bottom: -10px;
}

.equation-term .arrow-container .arrow {
    border: 8px solid transparent;
}

.equation-term .arrow-container.up .arrow {
    border-bottom: 6px solid hsl(200, 10%, 85%);
}

.equation-term .arrow-container.down .arrow {
    border-top: 6px solid hsl(200, 10%, 85%);
}

.equation-term .arrow-container.up:hover .arrow {
    border-bottom: 6px solid hsl(200, 30%, 50%);
}

.equation-term .arrow-container.down:hover .arrow {
    border-top: 6px solid hsl(200, 30%, 50%);
}

/***** Media Queries *****/

@media (max-width: 850px) {
    .main-equation {
        font-size: 18px;
    }

    .main-equation.homogeneous {
        font-size: 14px;
    }

    .equation-term {
        margin: 8px 0;
    }

    .term-wrapper {
        margin-left: 5px;
    }

    .equation-term .arrow-container {
        height: 30px;
        margin: -13px 0;
        width: 40px;
    }

    .equation-term .arrow-container .arrow {
        border: 6px solid transparent;
    }

    .equation-term .arrow-container.up .arrow {
        border-bottom: 4px solid hsl(200, 10%, 80%);
    }

    .equation-term .arrow-container.down .arrow {
        border-top: 4px solid hsl(200, 10%, 80%);
    }

    .equation-term .arrow-container.up:hover .arrow {
        border-bottom: 4px solid hsl(200, 30%, 50%);
    }

    .equation-term .arrow-container.down:hover .arrow {
        border-top: 4px solid hsl(200, 30%, 50%);
    }
}

.control-bar {
    padding: 0 20px;
    display: flex;
    flex-direction: row;
}

.control-bar .equation-container {
    display: flex;
    flex-direction: row;
    justify-content: center;

    overflow: hidden;
    flex-grow: 1;
}

.control-bar .options {
    white-space: nowrap;
    display: inline-block;
}

.control-bar .mobile-view-switcher {
    position: fixed;
    top: 70px;
    left: 10px;

    color: hsla(200, 10%, 90%, 0.2);
    background-color: hsla(200, 10%, 90%, 0.1);
    border: 1px solid hsla(200, 10%, 90%, 0.1);
    border-radius: 8px;
    overflow: hidden;

    background-filter: blur(3px);

    display: none;
    flex-direction: column;
    align-items: center;
}

.control-bar .mobile-view-switcher .entry {
    padding: 8px;
}

.control-bar .mobile-view-switcher .entry.selected {
    background-color: hsl(200, 30%, 85%);
    color: hsl(200, 10%, 15%);
}

/***** Media Queries *****/
@media (max-width: 850px) {
    .control-bar {
        padding: 0 10px;
    }
}

@media (max-width: 700px) {
    .control-bar {
        padding: 0;
    }

    .control-bar .options {
        width: 1px;
        overflow: hidden;
    }

    .control-bar .mobile-view-switcher {
        display: flex;
    }
}

.info-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: white;

    color: hsl(200, 10%, 10%);

    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 15px;

    padding: 15px 25px;
    box-shadow: 0px -2px 4px hsla(200, 10%, 10%, 0.3);
    border-top: 1px solid hsl(200, 10%, 80%);

    overflow: hidden;
}

.info-bar h2 {
    font-size: 12px;
    color: hsl(200, 10%, 40%);
    font-weight: 400;

    margin: 0;
    margin-bottom: 5px;
}

.info-bar .faded {
    color: hsl(200, 10%, 40%);
}

@media (max-width: 1000px) {
    .info-bar {
        grid-template-columns: 1fr 1fr;
    }

    .info-bar .group-structure {
        display: none;
    }
}


@media (max-width: 600px) {
    .info-bar {
        grid-template-columns: 2fr 1fr;
        padding: 15px;
    }

    .info-bar .reduced-form {
        display: none;
    }

    .info-bar .group-structure {
        display: inline;
        display: initial;
    }
}

#main-view {
	height: 100%;
	width: 100%;
}

#root {
	width: 100%;
	height: 100%;
}

html, body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
}

